const renderMapUrl = (val, row) =>
    <a href={`https://www.google.com/maps?q=${row['lat']},${row['long']}`}>
        Google Maps
    </a>;

const tableColumns = [
    {title: 'Name', prop: 'name'},
    {title: 'City', prop: 'city'},
    {title: 'Street address', prop: 'street'},
    {title: 'Phone', prop: 'phone', defaultContent: '<no phone>'},
    {title: 'Map', render: renderMapUrl, className: 'text-center'},
];

class Table extends React.Components {
    
}

$(function () {
    ReactDOM.render(
        <ReactDataComponents.DataTable
            keys={['id']}
            columns={tableColumns}
            initialData={rows}
            initialPageLength={50}
            initialSortBy={{prop: 'name', order: 'ascending'}}
            pageLengthOptions={[5, 20, 50, 100]}
        />,
        document.getElementById('content')
    );

    $('#content').find('[data-reactroot]').removeClass('container');
});